<template>
    <div id="userblog">
      <section>
        <div class="author">
          <img  class="author_img" :src="users.avatar" >
          <span class="author_name">{{users.username}}</span>
        </div>
      </section>
      <section >
        <div class="item" v-for="blog in bloglist">
          <div class="time">
            <p>{{articleTime(blog.createdAt).date}}</p>
            <p>{{articleTime(blog.createdAt).month}}月</p>
            <p>{{articleTime(blog.createdAt).year}}</p>
          </div>
          <div class="title">
            <router-link :to="`/article/${blog.id}`">
              {{blog.title}}
            </router-link>
          </div>
          <p class="art_decor">
            <router-link :to="`/article/${blog.id}`">
            {{blog.description}}...
            </router-link>
          </p>
        </div>
      </section>
      <section>
        <div class="block">
          <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :total="total">
          </el-pagination>
        </div>
      </section>
    </div>
</template>

<script>
  import blog from "../api/blog";
    export default {
      name: "Userblog",
      data(){
        return {
          users:[],
          bloglist:[],
          total:1
        }
      },
      created() {
        this.userId = this.$route.params.userId;
        blog.getbloglist({userId:this.userId})
        .then(res=>{
          this.users = res.data[0].user;
          this.bloglist = res.data;
          this.total =res.total;
        })
      },
      methods:{
        handleCurrentChange(value){
          this.userId = this.$route.params.userId;
          blog.getbloglist({userId:this.userId,page:value})
            .then(res=>{
              this.bloglist = res.data;
              this.total = res.total;
            })
        },
        articleTime(time){
          let dataObj = typeof time === 'object' ? time:new Date(time);
          return {
            date:dataObj.getDate(),
           month: dataObj.getMonth()+1,
            year:dataObj.getFullYear()
          }
        }
      }
    }
</script>

<style scoped>
  #userblog{
    margin-bottom: 10px;
  }
  .author{
    padding: 20px 8px 15px 10px;
    border-bottom: 1px solid #cccccc;
  }
  .author_name{
    vertical-align: middle;
    margin-left: 20px;
  }
  .author_img{
    display:inline-block;
    width: 70px;
    height: 70px;
    border: 1px solid #aeca4f;
    border-radius: 50%;
    vertical-align: middle;
  }
  .time{
    grid-column:1/2;
    grid-row: 1/3;
    align-self: center;
    color: #666666;
    font-size: 15px;
    }
  .time p{
    text-align: center;
  }
  .time p:first-child{
    font-size: 26px;
  }
  .item{
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-columns: 80px 1fr ;
    margin: 20px 10px 5px 10px;
    padding: 1px 1px 20px 1px;
    border-bottom: 1px solid #cccccc;
  }
  .title{
    grid-row: 1/2;
    grid-column:2/3;
    align-self: center;
    margin-bottom: 5px;

  }
  .title a{
    text-decoration: none;
    font-size: 19px;
    color: #242d08;
  }
  .art_decor{
    grid-row: 2/3;
    grid-column:2/3;
    align-self: center;
    font-size: 14px;
    color: #242d08;
  }
  .art_decor a{
    text-decoration: none;
    font-size: 14px;
    color: #242d08;
  }
  .btn{
    grid-row: 3/4;
    grid-column:2/3;
    margin: 10px 0px 10px 0px;
    font-size: 15px;
    color:#ff9900;
  }
  .btn span{
    padding: 5px 5px;
    cursor:pointer;
  }
  .block{
    text-align: center;
    margin: 20px 0 20px 0;
  }
  /deep/ .el-pager li.active {
    color: #409F08;
  }
  /deep/ .el-pager li:hover{
    color: #409F08;
  }
  /deep/ .el-pagination button:hover{
    color: #409F08;
  }

</style>



